* {
    box-sizing: border-box;
}
a {
    text-decoration: none;
    display: inline-block;
}
ul, li {
    list-style: none;
    margin: 0;
    padding: 0;
}
.container {
    max-width: 1170px;
    margin: 0 auto;
    padding: 0 15px;
}
body {
    //font-family: 'Roboto', sans-serif;
    font-family: 'PT Serif', serif;
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    color: #5e5e5e;
}
header {
    font-family: 'Roboto', sans-serif;
}
//Header top
.header__top {
    background: #303030;
}
.header__btn {
    float: right;
    line-height: 36px;
    font-family: 'Roboto', sans-serif;
    color: #1b1b1b;
    background: #FFC222;
    padding: 5px 20px 4px 50px;
    font-size: 16px;
    position: relative;
}
.header__btn:before {
    font-family: 'icomoon';
    content: "\f0b1";
    position: absolute;
    left: 22px;
    color: #1b1b1b;
}
.header__contacts:after { //Решение проблемы с float задается родителю!
    content: '';
    clear: both;
    display: block;
}
.header__phone, .header__email {
    color: #cbcacb;
    letter-spacing: 0.25px;
    line-height: 45px;
    position: relative;
}
.header__phone {
    padding-left: 20px;
    padding-right: 30px;
}
.header__phone:before {
    font-family: 'icomoon';
    content: "\f095";
    position: absolute;
    left: 0;
    color: #FFC222;
}
.header__email {
    padding-left: 24px;
}
.header__email:before {
    font-family: 'icomoon';
    content: "\f003";
    position: absolute;
    left: 0;
    color: #FFC222;
}
//Header content
.header__content {
    padding: 34px 0 23px;
}
.header__content-inner {
    display: flex;
    justify-content: space-between;
}
.menu li {
    display: inline-block;
}
.menu a {
    color: #323232;
    font-size: 16px;
    line-height: 36px;
    border: 1px solid transparent;
    padding: 0 16px;
    transition: all 0.3s;
}
.menu a:hover {
    border-color: #FFC222;
}
.menu li + li {
    padding-left: 10px; //Зделать отступы только внутри, все li, кроме первого, отступят друг от друга на 10px
}